body{font-size:14px;font-family:"Montserrat", sans-serif;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;height:100%;margin:0 auto;overflow-x:hidden}*{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}*,*::before,*::after{box-sizing:border-box}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}button{cursor:pointer}button:focus{outline:none}h3{font-weight:500}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.grid-container{display:grid;grid-template-columns:200px 0.7fr 0.3fr;grid-template-areas:"sidenav main aside";grid-column-gap:2em;-webkit-column-gap:2em;column-gap:2em;height:100vh;padding:20px}.sidenav{grid-area:sidenav}.main{grid-area:main}.aside{grid-area:aside;background-color:#64a68e}.grid-main{display:grid;grid-template-rows:0.3fr 0.4fr 0.3fr;grid-template-areas:"welcome" "time" "courses";height:100%}.time{grid-area:time;display:grid;grid-template-columns:0.4fr 0.6fr;grid-column-gap:10px}.time>div{box-shadow:rgba(34,73,90,0.2) 4px 4px 13px 0px;border-radius:15px}.grid-nav{display:grid;grid-template-rows:1fr;grid-template-areas:"navigation" "goUpgrade";height:100%;grid-gap:1rem}.navigation{grid-area:navigation}.go-upgrade{grid-area:goUpgrade}.grid-aside{display:grid;grid-template-columns:1fr;grid-template-rows:0.3fr 0.2fr 0.5fr;grid-template-areas:"logout" "progress" "reminders"}header nav h1{font-weight:bold;font-size:20px}header nav .title{display:grid;grid-auto-flow:column;justify-content:center;align-items:center;grid-gap:1rem;padding-top:1rem}header nav .nav{display:grid;justify-content:center;align-content:center;grid-gap:4.5vh;margin:5rem 2rem 0rem 2rem;-webkit-padding-start:initial;padding-inline-start:initial}header nav .nav li{transition:0.4s}header nav .nav li:first-of-type p{color:black}header nav .nav li:hover{opacity:0.6;transform:scale(1.1)}header nav .nav li a{display:grid;align-items:center;justify-content:center;grid-auto-columns:0.4fr 1fr;grid-auto-flow:column;color:#22495a;text-decoration:none}header nav .nav li p{color:#bbbbd3;font-weight:500}header nav .nav-icon{max-width:50%}.upgrade{max-width:60%;margin:-75px auto}h4{color:#bbbcd4;text-align:center;line-height:1.7;margin:20px 0px;font-weight:500}span{font-weight:bold;color:black}header nav,header article div{box-shadow:rgba(34,73,90,0.2) 4px 4px 13px 0px;border-radius:15px}header article div{display:grid;justify-content:center;align-self:center;grid-gap:0.5em;padding:2rem}.btn-upgrade{margin:auto;width:135px;height:40px;box-shadow:7px 7px 25px 0 rgba(93,127,244,0.57);cursor:pointer;background-color:#5e80f4;color:#fff;border-radius:10px}.welcome{grid-area:welcome;margin-top:0.7rem}.welcome+div{margin-top:0.7rem}.welcome h2{color:#fd6472;font-size:20px;font-weight:bold;margin-bottom:1rem}.welcome h2+p{color:#787085}.welcome h3{font-size:17px;font-weight:500}.welcome>div{display:grid;grid-template-columns:1fr 0.8fr;align-items:center;background-color:#f7e5e9;margin:2rem 0 1rem 0;padding:2rem;border-radius:15px}.welcome>div>div{display:grid;grid-template-rows:1fr 1fr;align-items:center}.welcome>article>div{display:grid;grid-template-columns:1fr 1fr;justify-content:center;align-items:center}.img-welcome{width:80%;margin:-60px auto 0px auto}.search-icon{margin-left:auto;width:40px;padding:0.7rem;border-radius:10px;background-color:#eef2fd}@media screen and (height: 720px){header nav .nav{margin:4rem 2rem}.upgrade{max-width:60%}}.courses{grid-area:courses;overflow:auto;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.courses .courses-title{display:grid;grid-template-columns:1fr 1fr;justify-content:center;align-items:center;margin-top:2rem}.courses .courses-title h3{font-weight:500}.courses .courses-title button{display:flex;ju