itle{display:grid;grid-template-columns:1fr 1fr;align-items:center}.reminders .reminders-btn{display:flex;justify-content:flex-end;align-items:center;background:none}.reminders .reminders-notification{width:1.5rem}.reminders .reminders-icon{width:65%;margin:auto 0px;padding:0.7rem;border-radius:10px}.reminders .reminders-icon-alert{background-color:#fef2f3}.reminders .reminders-icon-message{background-color:#eef2fd}.reminders .reminders-list-container{height:100%;overflow-x:scroll;-webkit-mask-image:linear-gradient(to top, rgba(97,96,96,0.18), #000);mask-image:linear-gradient(to top, rgba(97,96,96,0.18), #000)}.reminders .reminders-list-container ul{height:215px}.reminders .reminders-list-container ul h3{font-size:13px}.progress ul,.reminders ul{margin:1rem 0px}.progress li,.reminders li{display:grid;grid-template-columns:0.2fr 0.8fr;margin:1rem auto}.progress p,.reminders p{color:#a3a3c3;font-size:12px;font-weight:500}.progress{grid-area:progress;line-height:1.7;margin:1rem}.progress li{grid-gap:1rem;grid-template-columns:0.2fr 0.6fr 0.4fr;align-items:center}.progress li>p{margin:auto;padding:0.55rem 0.85rem;border-radius:10px;color:black;background-color:#f5f5f7}.progress li div>p{font-size:10px}.progress p{margin:auto;border-radius:7px}.apexcharts-gridlines-horizontal{display:none}progress[value]::-webkit-progress-bar{background-color:#eee;border-radius:2px;box-shadow:0 2px 5px rgba(0,0,0,0.25) inset}progress[value]::-webkit-progress-value{background-color:#5f82f3;border-radius:2px}progress{height:5px !important;width:inherit !important}.apexcharts-legend-text{font-family:"Montserrat", sans-serif !important;color:#a3a3c3 !important;font-weight:bold !important;font-size:10px !important}.apexcharts-text tspan{font-family:"Montserrat", sans-serif !important;color:#a3a3c3 !important;font-size:12px}

